body {
  background-color: rgba(0, 150, 100, 1);
}
.mainSection {
  background-color: rgba(255, 255, 255, 1);
  padding: 20px;
  max-width: 600px;
  margin: 40px auto;
  overflow: scroll;
}
.mainH1 {
  text-align: center;
  font-size: 30px;
}
.difficultyHeader {
  text-align: center;
  font-size: 25px;
}
.difficultyButton {
  display: block;
  margin: 10px auto;
  width: 200px;
  height: 40px;
  font-size: 20px;
}
.boardTable {
  margin: auto;
  border: 3px solid rgba(0, 0, 0, 1);
  padding: 5px;
}
.boardCell {
  width: 30px;
  height: 30px;
  background-color: rgba(200, 200, 200, 1);
  font-size: 20px;
  border: 1px solid rgba(0, 0, 0, 1);
}
.boardCell:active {
  background-color: rgba(150, 150, 150, 1);
}
.cellRevealed-true {
  background-color: rgba(255, 255, 255, 1);
}
.cellRevealed-false {
  min-width: none;
}
.cell-M.cellRevealed-true {
  background-color: rgba(200, 0, 0, 1);
}
.cell-0.cellRevealed-true {
  opacity: 0;
}
.cell-1 {
  color: rgba(100, 100, 255, 1);
}
.cell-2 {
  color: rgb(75, 175, 50);
}
.cell-3 {
  color: rgba(255, 0, 0, 1);
}
.cell-4 {
  color: rgba(0, 0, 255, 1);
}
.cell-5 {
  color: rgba(160, 100, 75, 1);
}
.cell-6 {
  color: rgba(0, 100, 100, 1);
}
.cell-7 {
  color: rgba(0, 0, 0, 1);
}
.cell-8 {
  color: rgba(100, 100, 100, 1);
}
.resetButton {
  display: block;
  margin: 20px auto;
  width: 125px;
  height: 50px;
  font-size: 18px;
  border: 2px solid black;
  background-color: rgba(225, 225, 225, 1);
}
.timerDiv {
  height: 44px;
  line-height: 44px;
}
.timerText {
  height: 40px;
  width: 100px;
  display: block;
  text-align: center;
  font-size: 25px;
  margin: 20px auto;
  border: 2px solid black;
}
.cellFlagged-true {
  color: black;
}
.gameOverH1 {
  text-align: center;
  font-size: 28px;
}
